        background-repeat: no-repeat;
        background-position: center center;
        background-color: rgba(#000, .6);
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        opacity: 0;
        transition: .3s; }
    &:hover:before {
            outline: 0;
            opacity: 1;
            transition: .3s; } }

.calc-btn {
    display: flex;
    margin: 0 auto;
    font-weight: 500;
    font-size: 16px;
    line-height: 0;
    color: #FFFFFF;
    background: #54B125;
    box-shadow: 0px 15px 40px rgba(84, 177, 37, 0.4);
    border-radius: 60px;
    border: none;
    width: 270px;
    padding: 35px 30px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: .4s;
    &:focus, &:active, &:hover {
        outline: 0;
        background: #9CCA2A;
        transition: .4s; } }
