.footer-wrap {
    display: flex; }

.footer-contact-container {
    background-image: url('../img/background-footer.jpg');
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: cover;
    padding: 70px 0 120px;
    width: 50%; }

.footer-map {
    width: 50%;
    overflow: hidden; }

.footer-container {
    width: 600px;
    max-width: 100%;
    margin-left: auto;
    padding: 0 75px 0 15px; }

.footer-title {
    font-weight: bold;
    font-size: 36px;
    line-height: 42px;
    text-transform: uppercase;
    color: #FFFFFF;
    max-width: 225px;
    margin-bottom: 40px; }

.footer-contacts {
    display: flex;
    margin-bottom: 30px; }

.footer-phones {
    flex-grow: 1; }

.footer-contacts-title {
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
    margin-bottom: 15px; }

.footer-contact {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-size: 13px;
    line-height: 15px;
    color: #FFFFFF;
    a {
        font-size: 18px;
        line-height: 21px;
        color: #FFFFFF;
        &:hover, &:active, &:focus {
            color: #fff;
            text-decoration: none; } } }

.footer-form {
    display: flex;
    flex-direction: column; }

.footer-submit-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px; }

.footer-input {
    border: none;
    background: none;
    border-bottom: 1px solid #FFFFFF;
    color: #fff;
    padding: 13px 16px;
    height: 54px;
    margin-bottom: 15px;
    &::placeholder {
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
        transition: color .2s; }
    &:focus, &:hover, &:active {
        outline: 0; }
    &:focus::placeholder {
            color: transparent;
            transition: color .2s; } }

.footer-btn {
    border: none;
    line-height: 0;
    padding: 25px 64.25px;
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    background: #54B125;
    box-shadow: 0px 15px 40px rgba(84, 177, 37, 0.4);
    border-radius: 50px;
    transition: .4s;
    &:focus, &:hover, &:active {
        outline: 0;
        background: #9CCA2A;
        transition: .4s; } }

.confrim-text {
    font-size: 12px;
    line-height: 14px;
    color: #FFFFFF;
    max-width: 280px;
    margin: 0;
    margin-left: 20px; }

.footer-info {
    display: flex;
    align-items: center;
    padding: 40px 15px;
    .logo-container {
        flex-grow: 1; } }

.footer-work-time {
    font-size: 14px;
    line-height: 17px;
    margin: 0 110px 0 0; }

.footer-social-icon {
    width: 16px;
    height: 16px;
    fill: #111;
    transition: .3s; }

.footer-social-list {
    margin: 0 -10px;
    display: flex; }

.footer-social a {
    width: 50px;
    height: 50px;
    background: #E6E6E6;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 0;
    margin: 0 10px;
    &:hover, &:active, &:focus {
        outline: 0;
        .footer-social-icon {
            fill: #54B125;
            transition: .3s; } } }
