@import "../../node_modules/bootstrap/scss/bootstrap-reboot.scss";

$menu-margin: 45px;

* {
    box-sizing: border-box; }


body {
    font-family: TT Norms, sans-serif;
    font-size: 18px;
    color: #111; }

.container {
    margin: 0 auto;
    width: 1200px;
    padding: 0 15px;
    max-width: 100%; }

ul {
    padding: 0;
    margin: 0;
    list-style: none; }

.nav-btn {
    padding: 23px 25.5px;
    line-height: 0;
    border: 2px solid #54B125;
    border-radius: 50px;
    font-size: 14px;
    background: transparent;
    transition: .3s;
    &:hover, &:active, &:focus {
        outline: 0;
        background-color: #54B125;
        color: #fff;
        transition: .3s; } }

.section-title {
    padding: 0 15px;
    text-align: center;
    font-weight: bold;
    font-size: 32px;
    line-height: 38px;
    text-transform: uppercase;
    margin-bottom: 12px; }


.section-short-desc {
    line-height: 21px;
    text-align: center; }


@import "../fonts/stylesheet.css";
