.first-section {
    background-image: url('../img/background.jpg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover; }

.first-section-container {
    background-image: url('../img/header-img.png');
    background-repeat: no-repeat;
    background-position: calc(100% + 60px) center;
    padding: 230px 15px; }

.first-section-title {
    max-width: 555px;
    font-weight: bold;
    font-size: 36px;
    line-height: 42px;
    color: #FFFFFF;
    margin-bottom: 30px; }

.first-section-desc {
    max-width: 655px;
    font-size: 20px;
    line-height: 24px;
    color: #FFFFFF;
    margin-bottom: 60px; }

.first-section-more {
    display: block;
    width: 270px;
    background: #54B125;
    box-shadow: 0px 15px 40px rgba(84, 177, 37, 0.4);
    border-radius: 70px;
    color: #fff;
    line-height: 0;
    padding: 35px 55px;
    position: relative;
    &:focus, &:hover, &:active {
        color: #fff;
        text-decoration: none;
        .more-arrow {
            padding-left: 10px;
            transition: .2s; } } }

.more-arrow {
    transition: .2s;
    position: absolute;
    top: 5px;
    right: 5px;
    height: 60px;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #9CCA2A;
    border-radius: 50%; }

.icon-btn-arrow {
    height: 16px;
    width: 16px; }

