.advantages-section {
    padding: 95px 0 85px; }

.advantages {
    display: flex;
    justify-content: space-between; }

.advantage {
    display: flex;
    flex-direction: column;
    align-items: center; }

.advantage-container {
    width: 120px;
    height: 120px;
    background: #F4F9F2;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 25px; }

.icon-advantage {
    height: 60px;
    width: 60px; }

.advantage-desc {
    width: 170px;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #111; }
