.products {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    margin-top: 55px;
    margin-bottom: 100px; }

.product {
    display: flex;
    flex-direction: column;
    width: 25%;
    padding: 0 15px;
    margin-bottom: 30px; }

.product-content {
    background: #F4F9F2;
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.06);
    border-radius: 10px;
    overflow: hidden;
    height: 100%; }

.product-link {
    height: 100px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    color: #111;
    transition: .3s; }

.product-content {
    &:focus, &:hover, &:active {
        text-decoration: none;
        .product-link {
            color: #fff;
            background-color: #54B125;
            transition: .3s; } } }

.catalog-link {
    color: #54B125;
    transition: .3s;
    box-shadow: none;
    &:focus, &:hover, &:active {
        color: #54B125;
        text-decoration: none;
        transition: .3s;
        .catalog-plus {
            font-size: 130px;
            transition: .2s; } } }

.catalog-plus {
    font-size: 122px;
    text-align: center;
    line-height: 0;
    margin: 135px 0;
    transition: .2s; }

.catalog-desc {
    font-weight: 500;
    line-height: 21px;
    text-align: center;
    color: #111; }
