.nav-container {
    padding-top: 55px;
    display: flex;
    justify-content: space-between;
    align-items: center; }

.logo-container {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #111;
    display: flex;
    align-items: center;
    &:focus, &:hover, &:active {
        color: #111;
        text-decoration: none; } }

.logo {
    margin-right: 10px;
    width: 48px;
    height: 60px; }

.logo-desc {
    max-width: 255px;
    margin: 0; }

.nav-wrap {
    display: flex;
    justify-content: flex-end;
    flex-grow: 1; }

.nav-items {
    display: flex;
    align-items: center;
    text-transform: uppercase;
    a {
        color: #111;
        font-size: 13px;
        font-weight: bold;
        transition: .3s;
        &:focus, &:hover, &:active {
            color: #54B125;
            text-decoration: none;
            transition: .3s; } } }

.nav-current a {
    color: #54B125;
    text-decoration: none;
    transition: .3s; }

.nav-item {
    margin-right: $menu-margin;
    position: relative;
    &:not(:nth-last-child(1)):after {
        content: '';
        width: 3px;
        height: 3px;
        background: #c4c4c4;
        position: absolute;
        top: 50%;
        right: -22.5px; } }

.nav-item:nth-last-child(1) {
    margin-right: 30px; }
