.top-contacts {
    width: 100%;
    background-color: #5D5250;
    font-size: 14px;
    color: #fff;
    height: 60px;
    display: flex;
    align-items: center; }

.top-contacts-icons {
    width: 15px;
    height: 15px;
    margin-right: 10px; }

.top-contacts-container {
    display: flex;
    justify-content: space-between; }

.top-left,
.top-right {
    display: flex;
    width: 50%;
    justify-content: space-between;
    align-items: center; }

.top-right {
    justify-content: flex-end;
    font-weight: 500;
    .top-col {
        margin-left: 30px; } }

.top-col {
    display: flex;
    align-items: center;
    a {
        color: #fff;
        &:hover, &:focus, &:active {
            color: #fff;
            text-decoration: none; } } }

.location-icon {
    width: 11px; }
